import React from 'react';
import {
    Link as LinkCore,
    RouteProps,
    generatePath,
    matchPath,
    // useHistory,
    useLocation
} from 'react-router-dom';

export const routes: Array<RouteOptions> = [];

export interface RouteOptions extends RouteProps {
    load?: () => Promise<{ default: React.ComponentType<any> }>;
    name?: string;
}

export const register = (route: RouteOptions) => {

    routes.push(route);
}

export const toPath = (to: string, state = {}) => {

    const route = routes.find((route) => route.name === to);

    const path = route ? generatePath(route.path as string, state) : to;

    return path;
}
export const useRouter = () => {

    const result: any = {
        toPath
    };

    // const history = useHistory();

    const location = useLocation();

    Object.defineProperty(
        result,
        'match',
        {
            get() {

                for (let i = 0; i < routes.length; i++) {

                    const route = routes[i];

                    const match = matchPath(location.pathname, route);

                    if (match) return match;
                }

                return {};
            },
            enumerable: true,
            configurable: true
        }
    );

    return result;
};

export const Link: React.FC<{ to: string, state?: any }> = ({ children, to, state }) => {

    return <LinkCore to={toPath(to, state)}>{children}</LinkCore>
}