import React, { Suspense } from 'react';
import { BrowserRouter, Switch, Route } from 'react-router-dom';
import { Router } from 'src/services';

const Loading: React.FC = () => <span>Loading...</span>;

const Component: React.FC<{ value: any }> = ({ value }) => {

    const Lazy = React.lazy(value);

    return <Lazy />
};

const Root: React.FC = () => {
    return (
        <BrowserRouter>
            <Switch>
                {Router.routes.map((route: any) => (
                    <Route key={route.name || route.path} {...route}>
                        <Suspense fallback={<Loading />}>
                            <Component value={route.load} />
                        </Suspense>
                    </Route>
                ))}
            </Switch>
        </BrowserRouter>
    )
}

export default Root;
